.banner{
    background: url('../images/banner.svg');
    background-position: bottom right;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    display: flex;
    align-items: center;
    .top-banner-content{
        margin-top: 30px;
        .banner-title{
            font-size: 40px;
            font-weight: 900;
        }
    }
}