.main-nav{
    padding: 25px 30px;
    transition: .3s ease .02s;
    @include desktop{
        background-color: $primary-color;
    }
    .navbar-nav{
        .nav-item{
            .nav-link{
                color: $white;
                padding-left: 20px;
                padding-right: 20px;
                font-size: 15px;
                text-transform: capitalize;
                letter-spacing: 1px;
                position: relative;
                z-index: 1;
                font-weight: 500;
            }
        }
        @include desktop{
            text-align: center;
        }
    }
    .navbar-brand{
        margin-right: 0;
        text-align: center;
        img{
            max-width: 150px;
        }
        color: $white;
        font-weight: 600;
        font-size: 25px;
    }
    .navbar-toggler{
        outline: 0;
        color: $white;
        cursor: pointer;
    }
    &.nav-bg{
        background: $primary-color;
        padding: 20px;
        border: none;
        box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.09);
    }
}

// Headroomjs Styles
.headroom {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transition: all .8s ease-in-out;
}
.headroom--unpinned {
    top: -500px;
    @include desktop{
        top: 0;
    }
}
.headroom--pinned {
    top: 0;
}