body{
    background-color: $body-color;
}

*{
    font-family: 'Roboto', sans-serif !important;
}

[class^="ti-"], [class*=" ti-"]{
    font-family: 'themify' !important;
}

.section{
    padding: 100px 0;
}

.form-control {
    height: 55px;
}

textarea.form-control{
    height: auto;
}

.h1 span,
.h2 span,
.h3 span,
.h4 span,
.h5 span,
.h6 span,
h1 span,
h2 span,
h3 span,
h4 span,
h5 span,
h6 span {
    color: $primary-color;
}

.grid-margin {
    margin-bottom: 30px;
}

@media (min-width:576px) {
    .grid-margin-sm-0 {
        margin-bottom: 0
    }
}

@media (min-width:768px) {
    .grid-margin-md-0 {
        margin-bottom: 0
    }
}

@media (min-width:992px) {
    .grid-margin-lg-0 {
        margin-bottom: 0
    }
}

@media (min-width:1200px) {
    .grid-margin-xl-0 {
        margin-bottom: 0
    }
}

.text-primary-theme{
    color: $primary-color !important;
}

.card{
    border: none;
    border-radius: 8px;
    box-shadow: $main-shadow;
}

.page-body-wrapper {
    min-height: 100vh;
    background: $body-color;
    padding-left: 0;
    padding-right: 0;
    padding-top: 70px;
}

.form-control-transparent {
    background-color: transparent!important;
    font-size: 16px;
}

.form-control-transparent,
.form-control-transparent:focus {
    border: 1px solid #fff;
    border-radius: 6px;
    color: #fff;
}

.form-control-transparent::-webkit-input-placeholder {
    color: #fff;
}

.form-control-transparent::-moz-placeholder {
    color: #fff;
}

.form-control-transparent:-ms-input-placeholder {
    color: #fff;
}

.form-control-transparent:-moz-placeholder {
    color: #fff;
}

.page{
    &__header{
        padding: 200px 0 100px;
        background-image: url('../images/breadcrumb.svg');
        background-size: cover;
        background-position: bottom center;
    }
    &__title{
        color: $white;
        margin-bottom: 30px;
    }
    &__breadcrumb{
        li{
            color: $white;
            font-size: 18px;
            a{
                color: $white;
                font-weight: 700;
                text-decoration: none;
                transition: .3s ease;
                i{
                    font-size: 14px;
                    display: inline-block;
                }
            }
        }
    }
}

.border-right{
    border-right: 1px solid $border-color;
}

.border-bottom{
    border-bottom: 1px solid $border-color;
}

.service-dropdown{
    .dropdown-item{
        cursor: pointer;
        font-size: 13px;
    }
}

.service-list{
    .btn-info{
        background: transparent;
        border: 1px solid $primary-color;
        font-weight: normal;
    }
}

.badge-success{
    background-color: #46ce6e;
    font-weight: normal;
}

.badge-warning{
    background-color: #ff5e54;
    font-weight: normal;
}