.social-box{
    .card {
        background: $white;
        border-radius: 8px;
        box-shadow: $main-shadow;
        border: 1px solid #d5dcec;
        transition: box-shadow .5s ease;
        border: none;
        // &:hover{
        //     box-shadow: 2px 4px 8px 0 rgba(46, 61, 73, .2)
        // }
    }
    &.card-item-preview{
        position: relative;
        .badge {
            position: absolute;
            top: -25px;
            right: -35px;
            width: 65px;
            height: 65px;
            padding: 1rem;
            border-radius: 50%;
            line-height: 32px;
            font-size: 1rem
        }
        .card-img-top {
            border-top-left-radius: 10px;
            border-top-right-radius: 10px
        }
    }
}

.social-box-item{
    text-align: left;
    box-shadow: none;
    display: flex;
    align-self: center;
    .box-body {
        padding: 40px 30px;
        .card-icon {
            width: 80px;
            height: 80px;
            margin-bottom: 2rem;
        }
    }
}

.comparision p {
    margin-bottom: .5rem;
    margin-top: .5rem;
    font-size: 13px
}

.comparision p i {
    color: #4cceac
}

.comparision_negative p i {
    color: #db504a
}

.comparision-us {
    background: #2370fb;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    color: $white;
    i{
        font-size: 30px !important;
    }
}

.comparision-others {
    background: #FFCADE;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    color: #E24747;
    i{
        font-size: 30px !important;
    }
}

.comparision{
    .card-body{
        padding: 30px 20px !important;
        p{
            margin: 0;
            padding: 10px 0;
            font-weight: 400;
            font-size: 16px;
        }
    }
}