.btn {
    font-size: 16px;
    border-radius: 4px;
    padding: 18px 55px;
    border: none;
    letter-spacing: 1px;
    font-weight: 500;
    transition: .3s ease;
    &:hover {
        box-shadow: $main-shadow;
    }
}

.btn-outline-primary{
    border: 1px solid $primary-color !important;
    color: $primary-color;
    &:hover{
        background: $primary-color;
        color: $white;
    }
}

.btn-primary {
    background: $primary-color;
    color: $white;
    &:hover {
        background: $primary-color;
        color: $white;
    }
}

.btn-primary-gradient {
    background-image: linear-gradient(-109deg, #FF5787 12%, #FF7C5F 100%);
    color: $white;
    &:hover {
        color: $white;
    }
    &:active{
        background-image: linear-gradient(-109deg, #FF5787 12%, #FF7C5F 100%);
        color: $white;
    }
}

