.banner {
  background: url("../images/banner.svg");
  background-position: bottom right;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  display: flex;
  align-items: center;
}

.banner .top-banner-content {
  margin-top: 30px;
}

.banner .top-banner-content .banner-title {
  font-size: 40px;
  font-weight: 900;
}

.social-box .card {
  background: #fff;
  border-radius: 8px;
  box-shadow: 0px 15px 30px 0px rgba(0, 0, 0, 0.06);
  border: 1px solid #d5dcec;
  transition: box-shadow .5s ease;
  border: none;
}

.social-box.card-item-preview {
  position: relative;
}

.social-box.card-item-preview .badge {
  position: absolute;
  top: -25px;
  right: -35px;
  width: 65px;
  height: 65px;
  padding: 1rem;
  border-radius: 50%;
  line-height: 32px;
  font-size: 1rem;
}

.social-box.card-item-preview .card-img-top {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.social-box-item {
  text-align: left;
  box-shadow: none;
  display: flex;
  align-self: center;
}

.social-box-item .box-body {
  padding: 40px 30px;
}

.social-box-item .box-body .card-icon {
  width: 80px;
  height: 80px;
  margin-bottom: 2rem;
}

.comparision p {
  margin-bottom: .5rem;
  margin-top: .5rem;
  font-size: 13px;
}

.comparision p i {
  color: #4cceac;
}

.comparision_negative p i {
  color: #db504a;
}

.comparision-us {
  background: #2370fb;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  color: #fff;
}

.comparision-us i {
  font-size: 30px !important;
}

.comparision-others {
  background: #FFCADE;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  color: #E24747;
}

.comparision-others i {
  font-size: 30px !important;
}

.comparision .card-body {
  padding: 30px 20px !important;
}

.comparision .card-body p {
  margin: 0;
  padding: 10px 0;
  font-weight: 400;
  font-size: 16px;
}

.reviews .card {
  border-radius: 6px;
  box-shadow: 0px 15px 30px 0px rgba(0, 0, 0, 0.06);
}

.form-control-search {
  height: 60px;
}

.footer {
  padding: 240px 0 70px;
  transition: all .25s ease;
  background: url("../images/footer-bg.svg");
  background-size: cover;
}

.footer .footer-logo {
  font-size: 40px;
  color: #5A95FF;
  font-weight: 600;
}

.footer a {
  color: #fff;
}
